export const itLocal = () =>{
    let locator;
    if(window.location.hostname === "localhost") {locator = "http://app." + window.location.hostname + ":3000";}
    else {if (window.location.hostname === "app.localhost") {locator = "http://" + window.location.hostname + ":3000";}
    else {if (window.location.hostname.split('.').slice(0, 1) === "app"){locator = "http://" + window.location.hostname;} 
    else {locator = "http://app." + window.location.hostname;}}}
    
    return locator;
  } 

  export const itLocalhome = () =>{
    let locatorhome;
    if(window.location.hostname === "localhost") {locatorhome = "http://" + window.location.hostname + ":3000";}
    else {if (window.location.hostname === "app.localhost") {locatorhome = "http://" + window.location.hostname.split('.').slice(1, 2) + ":3000";}
    else {if (window.location.hostname.split('.').slice(0, 1) === "app"){locatorhome = "http://" + window.location.hostname.split('.').slice(1, 2).join('.').split('.').slice(2, 3);} 
    else {locatorhome = "http://" + window.location.hostname.split('.').slice(0, 1).join('.').split('.').slice(1, 2);}}}
    
    return locatorhome;
  } 