import '../css/signin.css';
import React from 'react';
import { useState } from 'react';
import { NavLink } from 'react-router-dom';
import { itLocal } from '../utils/contstants'

const TradeButton = (props) => {

    const [value, setValue] = useState('');
    const [card, setCard] = useState('');
    const [address, setAddress] = useState('');
   
    return (
        <div className='buttontrade-back'>
       
        <button 
        className= {`button filled-green-button login-form-button button-disabled tradebutton ${props.valueT>=100 ? 'active' : ''}`}
         disabled={(!props.valueP || !props.valueT || !props.valueC)}>Trade</button>
        
            <NavLink to={`${itLocal()}/signup`} className="route-link public-menu-link">
            <button 
        className= {`button filled-green-button login-form-button button-disabled tradebuttonsign ${props.valueT<100 ? 'active' : ''}`}
         disabled={(!props.valueP || !props.valueT || !props.valueC)}>Sign Up</button>
                  </NavLink>
            
        </div>
    )


}

export default TradeButton;