import '../css/signin.css';
import React from 'react';
import 'react-credit-cards/es/styles-compiled.css'
import './card.css'
import { useState } from 'react';
import _fetch from 'isomorphic-fetch';

import axios from "axios";



const Trade = ({ onChange }) => {

    const [kdata, setKdata] = useState('');
   
    

    const [fiat, setFiat] = useState('');
    const [cripto, setCrypto] = useState('');
    const [fiatOption, setFiatOption] = useState('USD');
    const [criptoOption, setCryptoOption] = useState('BTC');
    const [fiatVal, setFiatVal] = useState('ZUSD');
    const [criptoVal, setCriptoVal] = useState('XXBT');
   
   
  

    


    let urldata = "https://api.kraken.com/0/public/Ticker?pair=" + criptoOption + fiatOption  ;
    
    console.log(urldata);
    axios.get(urldata)
    .then(function (response) {
        const urlVal   = criptoVal + fiatVal ;
   
        let data = JSON.stringify(response.data.result[urlVal].b[0]);

      console.log(data);
      setKdata(JSON.parse(data))
      
    })
    .catch(function (error) {
      console.log(error);
    });

 
    console.log(kdata);
   
   
    const handleChangeVF = event => {
        onChange(event.target.value)
        setCrypto(event.target.value / kdata)
        setFiat(event.target.value )
        
      };
      const handleChangeVC = event => {
        setCrypto(event.target.value )
        setFiat(event.target.value * kdata)
      };

      const handleChangeF = event => {
        setFiatOption(event.target.value)
         setCrypto('')
        setFiat('')
        switch (event.target.value){
            case "EUR": setFiatVal(`ZEUR`);
            break;
            case "USD": setFiatVal('ZUSD');
            break;

        }
      };

      const handleChangeC = event => {
        setCryptoOption(event.target.value)
        setCrypto('')
        setFiat('')
        switch (event.target.value){
            case "BTC": setCriptoVal('XXBT');
            break;
            case "ETH": setCriptoVal(`XETH`);
            break;

        }
      };

    return (
        
        <div className='trade-back' >
        <div className='login-form-content'>
            
            <form >

            <select 
            name="fiatOption" 
            id="fiatOption" 
            className='fiatOption'  
            onChange={handleChangeF}>
  <option value="USD" selected>USD</option>
  <option value="EUR">EUR</option>
</select>
               
                <div className="input card-fiat">
                    <input
                        className='input-field'
                        type="number"
                        name="fiat"
                        placeholder=""
                        value={fiat}
                        onChange={handleChangeVF}
                    />
                </div>

                <select 
                name="criptoOption" 
                id="criptoOption" 
                className='criptoOption' 
                onChange={ handleChangeC}>
  <option value="BTC" selected>BTC</option>
  <option value="ETH">ETH</option>
</select>

                <div className="input card-cripto">
                    <input
                        className='input-field'
                        type="number"
                        name="cripto"
                        placeholder=""
                        value={cripto}
                        onChange={handleChangeVC}
                    />
                </div>

                
            </form>

        </div>
        </div>
    )


}



export default Trade;