import React from "react";
import bgimage from './img/bgLines.png';
import './css/main.css';
import Wallet_promo from './main/wallet-promo'
import Products from "./main/products";
import TradeWidget from "./trade-widget";




const Main = (props) => {

  

    return (
        <div className='app-main'>
            <main className="page-main home" style={{
                backgroundImage: `url(${bgimage})`
            }}>
                <div className="container home-content">
              
                    <Wallet_promo/>
                    <Products productsData={props.productsData}/>
                   
                    <TradeWidget/>
                </div>
            </main>
        </div>
    )
}

export default Main;