import React from "react";
import PayCard from "./trade-widget/card";
import Caddress from "./trade-widget/cripto-addres";
import './css/TradeWidget.css'
import Trade from "./trade-widget/trade";
import TradeButton from "./trade-widget/trade-button";
import { useState } from "react";


const TradeWidget = () => {



    const [valueT, setValueT] = useState('')
    const handleChangeT = (valueT) => {
        setValueT(valueT)
    }
    const [valueP, setValueP] = useState('')
    const handleChangeP = (valueP) => {
        setValueP(valueP)
    }
    const [valueC, setValueC] = useState('')
    const handleChangeC = (valueC) => {
        setValueC(valueC)
    }


    return (
        <div className="TradeWidget">
            <Trade onChange={handleChangeT} />
            <div className="TradeWidgetSet">
              
                <PayCard onChange={handleChangeP} />
                
            <div className="tradesignbutton">
                <Caddress onChange={handleChangeC} />
                <TradeButton valueT={valueT} valueP={valueP} valueC={valueC} className="" />

            </div>
            </div>
            <div>
            </div>
        </div>

    )

}

export default TradeWidget;